import React from "react";
import { BrowserRouter as Router } from "react-router-dom";
import { Navbar, Nav } from "react-bootstrap";

const NavBar = ({ currentPage, handlePageChange}) => {
  return (
    <div>
      <div className="row">
        <div className="col-md-12">
          <Router>
            <Navbar className="px-3" variant="dark" expand="lg" sticky="top">
              <Navbar.Brand>Muiasar Al Ani</Navbar.Brand>
              <Navbar.Toggle aria-controls="basic-navbar-nav" />
              <Navbar.Collapse id="basic-navbar-nav" className="float-to-right">
                <Nav className="mr-auto ">
                  <Nav.Link
                    className={
                      currentPage === "Home"
                        ? "active active-page-link"
                        : "active"
                    }
                    onClick={() => handlePageChange("Home")} >
                    Home
                  </Nav.Link>
                  <Nav.Link className={
                      currentPage === "Work"
                        ? "active active-page-link"
                        : "active"
                    }
                    onClick={() => handlePageChange("Work")}>
                    Work
                  </Nav.Link>
                  <Nav.Link
                    className="active"
                    href="https://www.linkedin.com/in/muiasar-al-ani-71476420b/"
                    target="_blank"
                  >
                    LinkedIn
                  </Nav.Link>
                  <Nav.Link
                    className="active"
                    href="https://github.com/Muiasar-Al-Ani"
                    target="_blank"
                  >
                    GitHub
                  </Nav.Link>
                  <Nav.Link
                    className={
                      currentPage === "AboutMe"
                        ? "active active-page-link"
                        : "active"
                    }
                    onClick={() => handlePageChange("AboutMe")}
                  >
                    About Me
                  </Nav.Link>
                  <Nav.Link className={
                      currentPage === "ContactMe"
                        ? "active active-page-link"
                        : "active"
                    }
                    onClick={() => handlePageChange("ContactMe")}>
                    Contact Me
                  </Nav.Link>
                  <Nav.Link
                    className="active"
                    href="assets/images/Muiasar-Al-Ani-Resume.pdf"
                    download
                  >
                    Download Resume
                  </Nav.Link>
                </Nav>
              </Navbar.Collapse>
            </Navbar>
            <br />
          </Router>
        </div>
      </div>
    </div>
  );
};

export default NavBar;

// float-to-right navbar-collapse collapse show
